  components: { SidebarSection, Avatar },
  data () {
    return {
      sections: {
        info: {
          title: 'INFO',
          items: [
            {
              name: 'Email',
              icon: 'mdi-email',
              text: 'ameer@uni-bremen.de',
            },
            {
              name: 'Birth Date',
              icon: 'mdi-cake-variant',
              text: 'May 2, 1993',
            },
            {
              name: 'Habitation',
              icon: 'mdi-map-marker',
              text: 'Mönchengladbach, Germany',
            },
          ],
        },
        hobbies: {
          title: 'HOBBIES',
          items: [
            {
              icon: 'mdi-auto-fix',
              text: 'Hackathons',
            },
            {
              icon: 'mdi-pencil ',
              text: 'Drawing',
            },
            {
              icon: 'mdi-violin',
              text: 'viola, piano & orchestra',
            },
            {
              icon: 'mdi-biohazard',
              text: 'Challenges',
            },
            {
              icon: 'mdi-walk',
              text: 'Running',
            },
            {
              icon: 'mdi-image-filter-hdr',
              text: 'Nature',
            },
            {
              icon: 'mdi-earth',
              text: 'Travel',
            },
          ],
        },
        otherActivities: {
          title: 'OTHER ACTIVITIES',
          items: [
            { text: ' - Role-model for young women at an expo of the "SMILE" project of the German Research Center for Artificial Intelligence' },
            { text: ' - Violist at the youth orchestra of the Tonhalle Düsseldorf' },
            { text: ' - Volunteering as a swimming teacher at the German Life Saving Association' },
          ],
        },
        languages: {
          title: 'LANGUAGES',
          items: [
            {
              text : 'German',
              value: 100,
            },
            {
              text : 'English',
              value: 90,
            },
            {
              text : 'Korean',
              value: 25,
            },
            {
              text : 'Latin',
              value: 50,
            },
          ],
        },
      },
    }
  },
}
</script>

<style scoped>
.sidebar{
    background: #2e2e2e;
}
</style>
